import React, { Component } from 'react';
import { connect } from 'react-redux';
import Header from '../components/header'
import Nav from '../components/nav';
import StateTitle from '../components/StateName';
import Card from '../components/Card';
import CardList from '../components/CardList';
import ViewButtons from '../components/ViewButtons'
import Pages from '../components/Pages'
import Loading from '../components/Loading'
import Failed from '../components/Failed'
import './App.css';
import { setSortType, setStateSelected, getStateInfo, setViewType, setPageOn } from '../actions';

// get state from reducers
const mapStateToProps = state => {
  return {
    sortType: state.updateSortType.sortType,
    stateSelected: state.updateStateSelected.stateSelected,
    isPending: state.updateStateInfo.isPending,
    stateName: state.updateStateInfo.stateName,
    pageCount: state.updateStateInfo.pageCount,
    cityList: state.updateStateInfo.cityList,
    viewType: state.updateViewType.viewType,
    page: state.updatePageOn.page,
    failed: state.updateStateInfo.failed
  }
}

// get actions from actions
const mapDispatchToProps = (dispatch) => {
  return {
    buttonChange: (sortSelected) => dispatch(setSortType(sortSelected)),
    setState: (stateSelected) => dispatch(setStateSelected(stateSelected)),
    fetchState: (stateName) => dispatch(getStateInfo(stateName)),
    setViewType: (viewType) => dispatch(setViewType(viewType)),
    setPageOn: (page) => dispatch(setPageOn(page))
  }
}

// determine the page count based on what the pageCount state is because
// it returns an incorrect value and this is the work around for it
const truePageCount = (originPageCount) => {
    if (parseInt(originPageCount) === 0) {
      return 1;
    } else {
      return parseInt(originPageCount);
    }
  }

// returns the cityList based on the page the user is on.
const getCityListCopy = (originCityList, page) => {
  let cityListCopy = [...originCityList];
  if (cityListCopy.length > 10 && (page) * 10 < cityListCopy.length) {
    return cityListCopy.slice((page - 1) * 10, page * 10)
  } else if (cityListCopy.length > 10 && (page + 1) * 10 > cityListCopy.length) {
    return cityListCopy.slice(page * 10)
  } else {
    return cityListCopy;
  }
}

class App extends Component {

  render() {
    // destructures all of the props and actions passed into this componenet
    // this allows for cleaner references to the props
    const { sortType, 
            stateSelected, 
            isPending, 
            stateName, 
            cityList,
            buttonChange,
            setState,
            fetchState,
            viewType,
            setViewType,
            pageCount,
            page,
            setPageOn,
            failed
          } = this.props;

    // set the pageCountCopy to what the truePageCount method returns
    var pageCountCopy = truePageCount(pageCount)

    // landing title
    var body = <h5 className='tc StateTitle mt6'> Please Select a State to Begin</h5>
    if (failed) {
      body = <Failed />
    }
    // landing page body
    if (isPending) {
        // if fetch call is pending and has not resolved or failed yet
        body = <div><Loading /></div>
    } else if (cityList.length != 0) {
      if (sortType != 'none') {
        // deliver the contents of the body if the sort type is none
        // this just means to sort by alphabetical order
        cityList.sort((a, b) => (a[sortType] > b[sortType]) ? 1 : -1);
        // sets the cityList to be displayed based on the page the user is on
        const cityListCopy = getCityListCopy(cityList, page);
        body =  <div>
                <StateTitle stateToShow={stateName} />
                <CardList cityList={cityListCopy} sortType={sortType} viewType={viewType} page={page}/>
                <Pages pageCount={pageCountCopy} setPageOn={setPageOn} page={page}/>
              </div>
      } else {
        // deliver contents of the body if the sort type is not none
        // this had to be put in an else statement because the sort syntax
        // is different for alphabetical and number based
        cityList.sort((a,b) => (a.name > b.name) ? 1 : -1)
        const cityListCopy = getCityListCopy(cityList, page);
        body =  <div>
                <StateTitle stateToShow={stateName} />
                <CardList cityList={cityListCopy} sortType={sortType} viewType={viewType} page={page}/>
                <Pages pageCount={pageCountCopy} setPageOn={setPageOn} page={page}/>
              </div>
      }
    }

    // return contents of the page
    return (
      <div>
        <Header/>
        <Nav buttonChange={buttonChange} setState={fetchState} cityList={cityList} setPageOn={setPageOn}/> 
        <ViewButtons setViewType={setViewType}/>
        {body}
      </div>
    )
  }
}

// connect the reducers and actions to this app which allows us to 
// map state and dispatch to props
export default connect(mapStateToProps, mapDispatchToProps)(App);
