import React from 'react';
import Card from './Card';

const CardList = ({ cityList, sortType, viewType, page }) => {
	// cardListStyle is determines if the user chose grid or list style
	var cardListStyle = {}
	if (viewType === 'grid') {
		cardListStyle = {
			marginLeft: '50px',
			marginRight: '50px',
		}
	}
	// loop through the city list and return a new card for every item in 
	// the city list
	return (
		<div class='cardlist' style={cardListStyle}>
			{
				cityList.map((city, i) => {
					return (
						<Card 
							key={i}
							lowPrice={cityList[i].gasoline.substring(0,4)}
							midPrice={cityList[i].midGrade.substring(0,4)}
							highPrice={cityList[i].premium.substring(0,4)}
							dieselPrice={cityList[i].diesel.substring(0,4)}
							city={cityList[i].name}
							number={i + 1 + ((page - 1) * 10)}
							sortType={sortType}
							viewType={viewType}
						/>
					)
				})
			}
		</div>
	)
}

export default CardList;