import React from 'react';
import list from './list.png'
import grid from './grid.png'

const ViewButtons = ({ setViewType }) => {
  // returns buttons that allow the user to switch between the different view options, grid or list
	return (
		<div class='view'>
			<div className="btn-group btn-group-toggle" data-toggle="buttons"> 
  					<label className="btn btn-secondary button grid" onClick={() => setViewType('grid')}>
    					<input type="radio" name="options" id="option1"/><img src={grid} />
  					</label>
  					<label className="btn btn-secondary button list active" onClick={() => setViewType('list')}>
    					<input type="radio" name="options" id="option2" checked/><img src={list} />
  					</label>
  			</div>
		</div>
	)
}

export default ViewButtons;