import React from 'react';
import { 
	CHANGE_SORT_TYPE,
	CHANGE_STATE_SELECTED,
	REQUEST_PENDING,
	REQUEST_RESOLVED,
	REQUEST_FAILED,
	CHANGE_VIEW_TYPE,
	SET_PAGE_COUNT,
	SET_PAGE_NUMBER
} from './constants.js';

// on page load sortType will always be none(alphabetical)
const initialSortType = {
	sortType: 'none'
}

// on page load there is no state selected
const initialStateSelected = {
	stateSelected: ''
}

// on page load there are no state selected
const initialStateInfo = {
	isPending: false,
	stateName: '',
	cityList: [],
	failed: false
}

// on page load, initial view type is in list form
const initialViewType = {
	viewType: 'list'
}

// on page load there are no pages to show
const initialPageCount = {
	pageCount: 0
}

// on page load the default initial page is set to 1 because there is alwys at least one page
const initialPage = {
	page: 1
}

// update the state of the sort type
export const updateSortType = (state=initialSortType, action={}) => {
	switch(action.type) {
		case CHANGE_SORT_TYPE:
			return Object.assign({}, state, { sortType: action.payload }); 
		default: 
			return state;
	}
}

// update the state of the state selected
export const updateStateSelected = (state=initialStateSelected, action={}) => {
	switch(action.type) {
		case CHANGE_STATE_SELECTED:
			return Object.assign({}, state, { stateSelected: action.payload});
		default:
			return state;
	}
}

// update the state of the state info by making fetch calls based on 
// the state selected by the user
export const updateStateInfo = (state=initialStateInfo, action={}) => {
	switch(action.type) {
		case REQUEST_PENDING: 
			return Object.assign({}, state, { isPending: true })
		case REQUEST_RESOLVED:
			return Object.assign({}, state, {	isPending: false, 
									    stateName: action.stateName, 
									 	cityList: action.cityList,
									 	pageCount: action.pageCount})
		case REQUEST_FAILED:
			return Object.assign({}, state, { isPending: false, failed: true })
		default:
			return state;
	}
}

// update the state of the view type
export const updateViewType = (state=initialViewType, action={}) => {
	switch(action.type) {
		case CHANGE_VIEW_TYPE:
			return Object.assign({}, state, { viewType: action.payload })
		default: 
			return state;
	}
}

// update the state of the page user is on
export const updatePageOn = (state=initialPage, action={}) => {
	switch(action.type) {
		case SET_PAGE_NUMBER:
			return Object.assign({}, state, {page: action.payload})
		default: 
			return state;
	}
}



