import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { createStore, combineReducers, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import { createLogger } from 'redux-logger';
import { updateSortType, updateStateSelected, updateStateInfo, updateViewType, updatePageOn } from './reducers';
import './index.css';
import './containers/Pacifico-Regular.ttf'
import 'tachyons';
import App from './containers/App';
import * as serviceWorker from './serviceWorker';

const logger = createLogger();

// applyMiddleware applies the middleware that we are interested in
// const store = createStore(updateSortType, applyMiddleware(logger));

const rootReducer = combineReducers({ updateSortType, updateStateSelected, updateStateInfo, updateViewType, updatePageOn })
const store = createStore(rootReducer, applyMiddleware(thunk, logger));
//const store = createStore(rootReducer, applyMiddleware(thunk));

ReactDOM.render(
  <React.StrictMode>
  	<Provider store={store}>
    	<App />
  	</Provider>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
