import React from 'react';

const Card = ({ lowPrice, midPrice, highPrice, dieselPrice, city, number, sortType, viewType }) => {
	// cardStyle determines the style of the card based on the if the user is selcting list or grid type
	var cardStyle = {}
	if (viewType === 'grid') {
		cardStyle = {
			marginLeft: '20px',
			marginRight: '20px',
			display: 'inline-grid',
		}
	}

	// the chain of if statements determines which price point to bold based on which gas type the user 
	// selected
	if (sortType === 'gasoline') {
		return (
			<div className="card center card-font" style={cardStyle}>
	  			<div className="card-header">
	    			{`${number}.`}	&nbsp; &nbsp; &nbsp; {`${city}`}
	  			</div>
	  			<ul className="list-group list-group-flush">
	   				<b><li className="list-group-item">Low: &nbsp; &nbsp; &nbsp; &nbsp; ${lowPrice}</li></b>
				    <li className="list-group-item">Mid: &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;${midPrice}</li>
				    <li className="list-group-item">High: &nbsp; &nbsp; &nbsp; &nbsp; ${highPrice}</li>
				    <li className="list-group-item">Diesel: &nbsp; &nbsp; &nbsp; ${dieselPrice}</li>
	  			</ul>
			</div>
		)
	} else if (sortType === 'midGrade') {
		return (
			<div className="card center card-font" style={cardStyle}>
	  			<div className="card-header">
	    			{`${number}.`}	&nbsp; &nbsp; &nbsp; {`${city}`}
	  			</div>
	  			<ul className="list-group list-group-flush">
	   				<li className="list-group-item">Low: &nbsp; &nbsp; &nbsp; &nbsp; ${lowPrice}</li>
				    <b><li className="list-group-item">Mid: &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;${midPrice}</li></b>
				    <li className="list-group-item">High: &nbsp; &nbsp; &nbsp; &nbsp; ${highPrice}</li>
				    <li className="list-group-item">Diesel: &nbsp; &nbsp; &nbsp; ${dieselPrice}</li>
	  			</ul>
			</div>
		)
	} else if (sortType === 'premium') {
		return (
			<div className="card center card-font" style={cardStyle}>
	  			<div className="card-header">
	    			{`${number}.`}	&nbsp; &nbsp; &nbsp; {`${city}`}
	  			</div>
	  			<ul className="list-group list-group-flush">
	   				<li className="list-group-item">Low: &nbsp; &nbsp; &nbsp; &nbsp; ${lowPrice}</li>
				    <li className="list-group-item">Mid: &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;${midPrice}</li>
				    <b><li className="list-group-item">High: &nbsp; &nbsp; &nbsp; &nbsp; ${highPrice}</li></b>
				    <li className="list-group-item">Diesel: &nbsp; &nbsp; &nbsp; ${dieselPrice}</li>
	  			</ul>
			</div>
		)
	} else if (sortType === 'diesel') {
		return (
			<div className="card center card-font" style={cardStyle}>
	  			<div className="card-header">
	    			{`${number}.`}	&nbsp; &nbsp; &nbsp; {`${city}`}
	  			</div>
	  			<ul className="list-group list-group-flush">
	   				<li className="list-group-item">Low: &nbsp; &nbsp; &nbsp; &nbsp; ${lowPrice}</li>
				    <li className="list-group-item">Mid: &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;${midPrice}</li>
				    <li className="list-group-item">High: &nbsp; &nbsp; &nbsp; &nbsp; ${highPrice}</li>
				    <b><li className="list-group-item">Diesel: &nbsp; &nbsp; &nbsp; ${dieselPrice}</li></b>
	  			</ul>
			</div>
		)
	} else {
		return (
			<div className="card center card-font" style={cardStyle}>
	  			<div className="card-header">
	    			{`${number}.`}	&nbsp; &nbsp; &nbsp; {`${city}`}
	  			</div>
	  			<ul className="list-group list-group-flush">
	   				<li className="list-group-item">Low: &nbsp; &nbsp; &nbsp; &nbsp; ${lowPrice}</li>
				    <li className="list-group-item">Mid: &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;${midPrice}</li>
				    <li className="list-group-item">High: &nbsp; &nbsp; &nbsp; &nbsp; ${highPrice}</li>
				    <li className="list-group-item">Diesel: &nbsp; &nbsp; &nbsp; ${dieselPrice}</li>
	  			</ul>
			</div>
		)
	}
}

export default Card;