import React from 'react';

const Pages = ({ pageCount, setPageOn, page }) => {
	// pageCounts is determined based on the number of pages passed in
	var pageCounts = []
	// for every pageCount, a new pagination option is added
	for (let pageNum = 1; pageNum <= pageCount; pageNum++) {
    if (pageNum === page) {
      pageCounts.push(<li key={pageNum.toString()} class="page-item"><a class="page-link active" href="#" onClick={(event) => setPageOn(pageNum)}>{pageNum}</a></li>)
    } else {
		  pageCounts.push(<li key={pageNum.toString()} class="page-item"><a class="page-link" href="#" onClick={(event) => setPageOn(pageNum)}>{pageNum}</a></li>)
	 }
  }
	// prev and next are the arrows on the pagination bar.
	// they must be disabled or active based on the state of the pages
	// and what page the user is on
	var prev = <li class="page-item">
      				<a class="page-link" href="#" aria-label="Previous" onClick={() => setPageOn(page - 1)}>
        				<span aria-hidden="true">&laquo;</span>
     				 </a>
    			</li>
    var next = <li class="page-item">
      				<a class="page-link" href="#" aria-label="Next" onClick={() => setPageOn(page + 1)}>
        				<span aria-hidden="true">&raquo;</span>
     				 </a>
    			</li>
    // prev and next arrow disabled when user is on page 1
	if (pageCount === 1) {
		prev = <li class="page-item disabled">
      				<a class="page-link" href="#" aria-label="Previous">
        				<span aria-hidden="true">&laquo;</span>
     				 </a>
    			</li>
    	next = <li class="page-item disabled">
      				<a class="page-link" href="#" aria-label="Next">
        				<span aria-hidden="true">&raquo;</span>
     				 </a>
    			</li>
    // prev arrow is disabled if pageCount is greater than 1 and user is on page 1 
	} else if (page === 1 && pageCount != 1) {
		prev = <li class="page-item disabled">
      				<a class="page-link" href="#" aria-label="Previous">
        				<span aria-hidden="true">&laquo;</span>
     				 </a>
    			</li>
   	// next arrow is disable if pageCount is greater than 1 and user is on the last page
	} else if (page === pageCount && pageCount != 1) {
		next = <li class="page-item disabled">
      				<a class="page-link" href="#" aria-label="Next">
        				<span aria-hidden="true">&raquo;</span>
     				 </a>
    			</li>
	}
	// return contents of the pagination bar
	return (
		<div class=''>
			 <ul class="pagination justify-content-center mv4">
    			{prev}
			    {pageCounts}
			    {next}
  			</ul>
	  	</div>
	)
}

export default Pages;