import React from 'react'
import { BlockReserveLoading } from 'react-loadingg'

const Loading = () => {
	// returns loading screen while fetch request is 
	// pending
	var loadingStyle = {
		margin: 'auto',
	}
	return (
		<div>
			<BlockReserveLoading style={loadingStyle} color='#3da9fc'/>
		</div>
	)
}

export default Loading;