import React from 'react';
import { 
	CHANGE_SORT_TYPE,
	CHANGE_STATE_SELECTED,
	REQUEST_PENDING,
	REQUEST_RESOLVED,
	REQUEST_FAILED,
	CHANGE_VIEW_TYPE,
	SET_PAGE_COUNT,
	SET_PAGE_NUMBER
} from './constants.js';

// action of setting sort type
export const setSortType = (sortSelected) => {
	return {
		type: CHANGE_SORT_TYPE,
		payload: sortSelected
	}
}

// action of setting the state selected
export const setStateSelected = (stateSelected) => {
	return {
		type: CHANGE_STATE_SELECTED,
		payload: stateSelected
	}
}

// action of getting the stateinfo by making a fetch call
export const getStateInfo = (stateName) => async (dispatch) => {
	try {
		const url = `https://api.collectapi.com/gasPrice/stateUsaPrice?state=${stateName}`
		
      	const response = await fetch(url, {
        headers: {
          'authorization' : 'apikey 4n8eNBnyCEvLguEAB75FQB:0l9SlcqKzA6ILHgHxKM9G0'
        }
      });
      dispatch({ type: REQUEST_PENDING })
      const data = await response.json();
      dispatch({ 	type: REQUEST_RESOLVED, 
      				stateName: data.result.state.name,
      				cityList: data.result.cities,
      				pageCount: data.result.cities.length / 10
      				 })
	} catch(error) {
		dispatch({ type: REQUEST_FAILED })
		console.log('Error: ', error);
	}
}

// set the view type 
export const setViewType = (viewType) => {
	return { 
		type: CHANGE_VIEW_TYPE,
		payload: viewType
	}
}

// set the page that the user is on
export const setPageOn = (pageNum) => {
	return {
		type: SET_PAGE_NUMBER,
		payload: pageNum
	}
}

